// Media
// Max
$maxMobile: "(max-width:767px)";
$maxSmall: "(max-width:991px)";
$MaxLarge: "(max-width:1200px)";
// Min
$min2xSmall: "(min-width: 576px)";
$minSmall: "(min-width:768px)";
$minMedium: "(min-width:992px)";
$minLarge: "(min-width:1200px)";
//
// Colours
$mainColor: rgb(31, 33, 34);
$secndColor: rgb(39, 41, 42);
$PinkRedColor: rgb(236, 96, 144);
$GrayscaleColor: rgb(102, 102, 102);
$White: white;
$WhiteGray: rgb(238, 238, 238);
// values
//main with %
$Primary-with-Percent: 100%;
//main with %
$Primary-with-px: 40px;
// main padding
$Primary-padding: 60px;
// main margin
$Primary-margin: 30px;
// main-border
$Primary-border: 23px;
//
$Percent-50: 50%;
$pixel-25: 25px;
$pixel-10: 10px;
$pixel-8: 8px;
$pixel-20: 20px;
$pixel-50: 50px;
$pixel-60: 60px;
$pixel-23: 23px;
// Transtions
$mainTranstion: all 0.3s;
// fonts
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;700&display=swap");
$font-Poppins: "Poppins", sans-serif;
// $bs-body-font-family: var(--bs-font-sans-serif);
$bs-font-sans-serif: system-ui, -apple-system, "Segoe UI", Roboto,
  "Helvetica Neue", "Noto Sans", "Liberation Sans", Arial, sans-serif,
  "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
$bs-font-monospace: "SFMono-Regular", "Menlo", "Monaco", "Consolas",
  "Liberation Mono", "Courier New", monospace;
//   fonts-sizes
$bs-body-font-size: 1rem;
$bs-body-font-weight: 400;
$bs-body-line-height: 1.5;
//
$bs-blue: #0d6efd;
$bs-indigo: #6610f2;
$bs-purple: #6f42c1;
